<template>
  <div class="rescue-container">
    <!--  -->
    <div class="test">
      <!-- <van-button type="primary" @click.native="getDrivingRoute"
        >getDrivingRoute</van-button
      > -->
    </div>
    <!--  -->
    <div id="amap" :style="{ height: clientHeight + 'px' }"></div>

    <!-- logo -->
    <div class="logo">
      <img :src="logoIcon" alt="" />
      <div>{{ curCity }}</div>
    </div>

    <div class="btn-group">
      <!-- AED -->
      <van-button
        size="small"
        color="#6ab3f8"
        type="info"
        @click.native="showAED"
      >
        {{ showAEDFlag ? "AED" : "AED" }}
      </van-button>
      <!--  -->
      <van-button
        size="small"
        color="#6ab3f8"
        type="info"
        @click.native="showTask"
        >{{ showTaskInfoFlag ? "" : "" }}</van-button
      >
    </div>

    <!--  -->
    <SearchSheet
      :showSearchSheetFlag.sync="showSearchSheetFlag"
      :surroundings="surroundings"
      :searchAreaList="searchAreaList"
      :title="searchSheetTitle"
      @select-location="selectLocation"
      @search-input="searchInput"
    />

    <!--  -->
    <ConfirmLocation
      @submitSuccess="submitSuccess"
      :showConfirmLocationFlag.sync="showConfirmLocationFlag"
      :yourLocation="centerLocation"
      :guid="guid"
    />

    <!--  -->
    <div class="center-icon">
      <transition
        enter-active-class="animate__animated animate__bounceIn "
        leave-active-class="animate__animated animate__bounceOut "
      >
        <div
          class="bubble animate__animated"
          :class="{ 'animate__pulse animate__faster': showBubbleAnimFlag }"
          v-show="showBubbleFlag"
          :style="{ display: centerLocation.address ? 'block' : 'none' }"
          @click="showSearchSheet"
        >
          <div class="triangle"></div>
          <div class="right-arrow">
            <img :src="rightArrowIcon" />
          </div>
          <div class="text">
            {{ centerLocation.address }}
          </div>
        </div>
      </transition>
      <img
        class="animate__animated"
        :class="{ 'animate__slideInDown animate__faster': showCenterAnimFlag }"
        :src="selectIcon"
        alt=""
        @click="toggleBubble"
      />
    </div>

    <!--  -->
    <div class="operation-container">
      <div class="statement" @click="showStatement">
        <img :src="statementIcon" />
      </div>
      <div class="refresh" @click="handleRefreshAnim">
        <img :class="{ 'rotate-anim': rotateAnim }" :src="refreshIcon" />
      </div>
      <div class="locate" @click="locateCurPosition(true)">
        <img :src="locateIcon" />
      </div>
    </div>

    <!--  -->
    <TaskInfo
      :showTaskInfoFlag="showTaskInfoFlag"
      :time="timeMin"
      :distance="distanceKM"
      :dispatchInfo="dispatchInfo"
    />

    <!--  -->
    <van-button
      class="share-location"
      type="info"
      round
      color="#6ab3f8"
      icon="location-o"
      @click="shareLocation"
      ></van-button
    >

    <!--  -->
    <van-action-sheet
      v-model="showNavigationFlag"
      :actions="navigationActions"
      cancel-text=""
      @select="navigationSelect"
      close-on-click-action
    />
  </div>
</template>

<script>
let _this;
import AMapLoader from "@amap/amap-jsapi-loader";
//
import meIcon from "@assets/img/me.png";
import carIcon from "@assets/img/car.png";
import aedIcon from "@assets/img/aedcx.png";
// 
import SearchSheet from "@components/Rescue/SearchSheet";
import TaskInfo from "@components/Rescue/TaskInfo";
import ConfirmLocation from "@components/Rescue/ConfirmLocation";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    //
    SearchSheet,
    TaskInfo,
    ConfirmLocation,
  },
  data() {
    _this = this;
    return {
      curCity: "", //
      guid: "", //
      clientHeight: document.documentElement.clientHeight,
      AMap: null,
      map: null,
      geolocation: null,
      rotateAnim: false,
      centerLocation: {}, //
      curLocation: {}, //
      // targetLocation: {}, //
      XZBM: "",
      surroundings: [],
      searchText: "", //
      searchSheetTitle: "",
      searchAreaList: [],
      showTaskInfoFlag: false, //TaskInfo
      showSearchSheetFlag: false,
      showConfirmLocationFlag: false,
      showBubbleAnimFlag: false,
      showCenterAnimFlag: true,
      showNavigationFlag: false,
      taskStartFlag: false,
      showBubbleFlag: false,
      confirmLocationFlag: false,
      showAEDFlag: false,
      //
      time: "",
      distance: "",
      // 
      refreshIcon: require("@assets/img/refresh.png"),
      locateIcon: require("@assets/img/locate.png"),
      selectIcon: require("@assets/img/select.png"),
      rightArrowIcon: require("@assets/img/right-arrow.png"),
      logoIcon: require("@assets/img/logo.png"),
      statementIcon: require("@assets/img/statement.png"),
      targetIcon: require("@assets/img/target.png"),
      ambulIcon: require("@assets/img/ambul2.png"),
      //
      centerMarker: null,
      ambulMarker: null, //
      meMarker: null, //
      targetMarker: null, //
      AEDMarkerList: null, //AED
      //
      AEDList: [], //AED
      navigationActions: [{ name: "" }, { name: "" }, { name: "" }],
      selectNavigationLocation: {},
      //
      dispatchInfo: {},
      //
      timer: null,
      locateTimer: null,
    };
  },
  mounted() {
    this.initMap(); //
    this.showStatementOnce(); //
  },
  beforeDestroy() {
    this.locateTimer && clearInterval(this.locateTimer);
    this.timer && clearInterval(this.timer);
    this.locateTimer = null;
    this.timer = null;
  },
  computed: {
    ...mapState(["targetLocation", "originAmbulLocation", "originAmbulFlags"]),
    distanceKM() {
      //
      const distance = (this.distance / 1000).toFixed(1);
      return distance > 0 ? distance + "" : "--- ";
    },
    timeMin() {
      //
      const time = Math.ceil(this.time / 60);
      return time > 0 ? time + "" : "--- ";
    },
  },
  //
  beforeRouteEnter(to, from, next) {
    console.log(to, from, next);
    const {
      query: { guid },
    } = to;
    const reg =
      /[a-z0-9]{5,20}-[a-z0-9]{3,10}-[a-z0-9]{3,10}-[a-z0-9]{3,10}-[a-z0-9]{5,20}/i;
    if (!reg.test(guid)) {
      next({
        path: "/error",
        query: { type: "0" },
      });
    }
    next(async (vm) => {
      vm.guid = guid;
      await vm.getFlag(guid);
    });
  },
  watch: {
    dispatchInfo(newV, oldV){
      console.log('dispatchInfo ',newV)
      const {address} = this.dispatchInfo;
      this.geocoder.getLocation(address, (type, res)=>{
        console.log(type,res)
        if(type === 'complete'){
          const {location:{lat,lng}, formattedAddress}= res.geocodes[0];
          this.setTargetLocation({
            lng,
            lat,
            address: formattedAddress
          });
          this.createTargetMarker();
        }else if(type === 'error'){

        }else{

        }
      });
    } 
  },
  methods: {
    ...mapMutations([
      "setTargetLocation",
      "setOriginAmbulLocation",
      "setOriginAmbulFlags",
    ]),
    //
    async initMap() {
      try {
        const AMap = await AMapLoader.load({
          key: "8355bf8c48ff24bfeb39e954ce51cfdc", // WebKey load 
          version: "1.4.15", //  JSAPI  1.4.15
          plugins: ["AMap.Geolocation", "AMap.Geocoder", "AMap.Driving"], // 'AMap.Scale'
          AMapUI: {
            //  AMapUI
            version: "1.1", // AMapUI  1.1
            plugins: ["overlay/SimpleMarker"], //  AMapUI ui
          },
          Loca: {
            //  Loca 
            version: "1.3.2", // Loca  1.3.2
          },
        });

        this.AMap = AMap;
        this.map = new AMap.Map("amap", {
          zoom: 20,
          viewMode: "2D",
        });

        this.geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, // true
          timeout: 3000, // 
          GeoLocationFirst: false,
          maximumAge: 0, //00
          // Pixel(10, 20)
          showButton: false,
          showMarker: false, //true
          panToLocation: false, //true
          showCircle: false, //true
          //  ,  RB
        });

        this.geocoder = new AMap.Geocoder({
          radius: 500, //radius
          extensions: "all", //base
        });

        this.driving = new AMap.Driving({
          policy: AMap.DrivingPolicy.LEAST_TIME, // AMap.DrivingPolicy.LEAST_TIME
          map: this.map,
          autoFitView: false,
          hideMarkers: true,
        });

        this.locateCurPosition(true); //
        setInterval(() => {
          this.locateCurPosition(); //
        }, 3000);

        this.createTargetMarker(); //

        // this.getDrivingRoute(); //
        this.timer = setInterval(() => {
          console.log("get route");
          this.getDrivingRoute();
        }, 5000);

        this.map.on("complete", () => {
          console.log("");
        });

        this.map.on("movestart", () => {
          console.log("movestart");
          this.showBubbleFlag = false;
          this.showCenterAnimFlag = false;
        });

        this.map.on("moveend", () => {
          console.log("moveend");
          this.showCenterAnimFlag = true;
          this.getLocation();
        });
      } catch (error) {
        this.$notify({
          type: "warning",
          message: ",",
          duration: 3000,
        });
      }
    },
    //
    locateCurPosition(moveFlag = false) {
      if (moveFlag) {
        this.showBubbleFlag = false;
        this.showCenterAnimFlag = false;
      }

      this.geolocation.getCurrentPosition((type, res) => {
        if (type === "complete") {
          const {
            position: { lat, lng },
            formattedAddress: address,
            addressComponent,
          } = res;

          const { city: curCity } = addressComponent;
          this.curLocation = {
            lat,
            lng,
            address,
          };
          this.curCity = curCity;

          this.meMarker && this.map.remove(this.meMarker);
          this.meMarker = this.createMarker({
            width: 40,
            height: 40,
            lng,
            lat,
            image: meIcon,
          });

          this.map.add(this.meMarker);

          if (moveFlag) {
            //
            this.map.setCenter([lng, lat]);
          }
        } else {
          this.$notify({
            type: "warning",
            message: "",
            duration: 3000,
          });
        }
      });
    },
    //
    getLocation() {
      const { lat, lng } = this.map.getCenter();
      //
      this.geocoder.getAddress(new this.AMap.LngLat(lng, lat), (type, res) => {
        console.log(type, res);
        if (type === "complete") {
          const {
            regeocode: {
              pois: surroundings,
              formattedAddress: address,
              addressComponent,
            },
          } = res;
          const { adcode: XZBM } = addressComponent;
          this.centerLocation = {
            address,
            lng,
            lat,
            addressComponent,
          };
          this.XZBM = XZBM;
          this.surroundings = surroundings;
          this.showBubbleFlag = true;
        } else {
          this.$notify({
            type: "warning",
            message: "",
            duration: 3000,
          });
        }
      });
    },
    //
    showSearchSheet() {
      this.showBubbleAnimFlag = true;
      this.$nextTick(() => {
        let timer = setTimeout(() => {
          this.showSearchSheetFlag = true;
          this.showBubbleAnimFlag = false;
          clearTimeout(timer);
          timer = null;
        }, 600);
      });
    },
    //AED
    showAED() {
      this.showAEDFlag = !this.showAEDFlag;
      if(this.showAEDFlag){
        this.getAED();
      }else{
        this.AEDMarkerList && this.map.remove(this.AEDMarkerList);
        this.map.setZoom(20);
      }
    },
    //
    submitSuccess() {
      this.setTargetLocation(this.centerLocation);
      this.createTargetMarker();
      this.getDrivingRoute();
    },
    //
    createAmbulMarker(){
      const { JD, WD } = this.dispatchInfo;
      const {lng, lat} = this.$locationUtils.wgs84Togcj02(JD, WD);
      this.ambulMarker && this.map.remove(this.ambulMarker);
      this.ambulMarker = this.createMarker({
        width: 40,
        height: 40,
        lng,
        lat,
        image: this.ambulIcon,
      });

      this.map.add(this.ambulMarker);
    },
    //
    createTargetMarker() {
      if (!this.targetLocation) return;
      const { lat, lng } = this.targetLocation;
      this.targetMarker && this.map.remove(this.targetMarker);
      this.targetMarker = this.createMarker({
        width: 35,
        height: 35,
        lng,
        lat,
        image: this.targetIcon,
      });

      this.map.add(this.targetMarker);
    },
    //
    showTask() {
      this.showTaskInfoFlag = !this.showTaskInfoFlag;
    },
    //
    showStatementOnce() {
      const statementFlag = localStorage.getItem("statementFlag");

      if (statementFlag != "1") {
        this.showStatement();
        localStorage.setItem("statementFlag", "1");
      }
    },
    //
    showStatement() {
      this.$dialog.alert({
        messageAlign: "left",
        title: "",
        message: ``,
      });
    },
    //
    navigationSelect(action, index) {
      let navigationUrl;
      switch (index) {
        case 0:
          //api.map.baidu.com/geocoder?location=39.990912172420714,116.32715863448607&coord_type=gcj02&output=html&src=webapp.baidu.openAPIdemo
          navigationUrl = `//api.map.baidu.com/geocoder?location=${this.selectNavigationLocation.GPS_Y_BD},${this.selectNavigationLocation.GPS_X_BD}&coord_type=gcj02&output=html&src=webapp.baidu.openAPIdemo`;
          window.open(navigationUrl);
          break;
        case 1:
          navigationUrl = `//uri.amap.com/marker?position=${this.selectNavigationLocation.GPS_X_GD},${this.selectNavigationLocation.GPS_Y_GD}&name=${this.selectNavigationLocation.ADDRESS}&src=mypage&coordinate=gaode&callnative=0`;
          window.open(navigationUrl);
          break;
        case 2:
          navigationUrl = `//apis.map.qq.com/uri/v1/geocoder?coord=${this.selectNavigationLocation.GPS_Y_TX},${this.selectNavigationLocation.GPS_X_TX}&referer=OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77`;
          window.open(navigationUrl);
          break;
        default:
          break;
      }
    },
    //AED
    addAEDMarker(AEDList) {
      this.AEDMarkerList && this.map.remove(this.AEDMarkerList);
      this.AEDMarkerList = AEDList.map(({ GPS_X, GPS_Y, ADDRESS, NAME }) => {
        const {lng, lat} = this.$locationUtils.wgs84Togcj02(GPS_X, GPS_Y);
        const marker = this.createMarker({
          lng,
          lat,
          // label: NAME,
          image: aedIcon,
        });

        marker.on("click", (e) => {
          this.selectNavigationLocation.GPS_X_GD = lng;
          this.selectNavigationLocation.GPS_Y_GD = lat;
          this.selectNavigationLocation.ADDRESS = ADDRESS;
          this.navigationSelect(null, 1);
        });
        return marker;
      });

      this.map.add(this.AEDMarkerList);
    },
    //
    async getFlag(guid) {
      try {
        let {
          data: { Flag, RWZT, XZBM },
        } = await this.$http({
          url: "AppRouteService.asmx/GetFlag",
          methods: "get",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          params: {
            guid,
          },
        });

        if (RWZT == "1") {
          this.setTargetLocation(null);
          // this.$router.push({
          //   path: "/complete",
          // });
          this.$dialog.alert({
            message: "",
            theme: "round-button",
          });
        }
      } catch (error) {
        console.log(error);
        this.$notify({
          type: "warning",
          message: "Flag",
          duration: 3000,
        });
      }
    },
    //
    async getDispatchInfo() {
      try {
        const {
          data: { isSucceed, resultData },
        } = await this.$http({
          url: "ZIT/getAMUTask",
          methods: "get",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          params: {
            guid: this.guid,
            XZBM: this.XZBM || 0,
          },
        });

        if (isSucceed) {
          const dispatchInfo = JSON.parse(resultData)[0];
          this.dispatchInfo = dispatchInfo;
        }
      } catch (error) {
        this.$notify({
          type: "warning",
          message: "",
          duration: 3000,
        });
      }
    },
    //
    async getDrivingRoute() {
      try {
        await this.getDispatchInfo();
        const { JD, WD } = this.dispatchInfo;
        const startPos = this.$locationUtils.wgs84Togcj02(JD, WD);
        if(!this.targetLocation) return; //
        this.drawDrivingRoute(startPos, this.targetLocation);
      } catch (error) {
        console.log(error);
        this.$notify({
          type: "warning",
          message: "",
          duration: 3000,
        });
      }
    },
    // 
    drawDrivingRoute(startPos, endPos) {
      console.log("", startPos);
      console.log("", endPos);

      startPos = [startPos.lng, startPos.lat];
      endPos = [endPos.lng, endPos.lat];
      this.driving.clear();
      this.driving.search(startPos, endPos, (type, res) => {
        if (type === "complete") {
          const route = res.routes[0];
          const { distance, time } = route;
          this.distance = distance;
          this.time = time;
          this.createAmbulMarker();
        } else {
          this.$notify({
            type: "warning",
            message: "",
            duration: 3000,
          });
        }
      });
    },
    //AED
    async getAED() {
      try {
        let {
          data: { isSucceed, resultData },
        } = await this.$http({
          url: "ZIT/getAED",
          methods: "get",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          params: {
            // XZBM: this.XZBM,
            XZBM: 320100,
          },
        });

        resultData = JSON.parse(resultData);
        if (isSucceed) {
          this.addAEDMarker(resultData);
          this.map.setZoom(14);
        }
      } catch (error) {
        this.$notify({
          type: "warning",
          message: "AED",
          duration: 3000,
        });
      }
    },
    //marker
    createMarker({
      width = 40,
      height = 40,
      lng = "",
      lat = "",
      image,
      label = "",
    }) {
      let options = {
        size: new this.AMap.Size(width, height), // 
        image, // Icon
        imageSize: new this.AMap.Size(width, height), // Icon
      };
      let icon = new this.AMap.Icon(options);
      // console.log(options)

      let marker = new this.AMap.Marker({
        position: new this.AMap.LngLat(lng, lat),
        offset: new this.AMap.Pixel(-width / 2, -height / 2),
        icon: icon, //  Icon 
        title: "",
      });
      label
        ? marker.setLabel({
            offset: new this.AMap.Pixel(0, -15),
            content: `<div class="amap-marker-label">${label}</div>`,
          })
        : null;

      return marker;
    },

    //
    searchInput(value) {
      this.AMap.plugin("AMap.PlaceSearch", () => {
        var placeSearch = new this.AMap.PlaceSearch({}); //
        placeSearch.search(value, (status, res) => {
          console.log(status, res);
          if (status === "complete") {
            this.searchAreaList = res.poiList.pois;
          } else {
            this.searchAreaList = [];
          }
        });
      });
    },
    //
    shareLocation() {
      //
      if (!this.centerLocation.lat) {
        this.$notify({
          type: "warning",
          message: "",
          duration: 3000,
        });
        return;
      }
      this.showConfirmLocationFlag = true;
    },
    //
    refresh() {
      location.reload();
    },
    //
    toggleBubble() {
      this.showBubbleFlag = !this.showBubbleFlag;
    },
    //
    handleRefreshAnim() {
      this.rotateAnim = true;
      let timer = setTimeout(() => {
        this.rotateAnim = false;
        clearTimeout(timer);
        timer = null;
        this.refresh();
      }, 500);
    },
    //
    selectLocation(location) {
      this.showSearchSheetFlag = false;
      this.moveToLocation(location);
    },
    //
    moveToLocation({ location }) {
      this.map.setCenter([location.lng, location.lat]);
      this.getLocation();
    },
  },
};
</script>

<style  >
/*  */
.rotate-anim {
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -moz-transform: rotate(360deg);
  -o-transform: rotate(360deg);
  -ms-transform: rotate(360deg);
}

/*  */
.operation-container {
  position: fixed;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.refresh,
.statement,
.locate {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
}

.refresh img,
.statement img,
.locate img {
  width: 30px;
  height: 30px;
  transition: All 0.4s ease-in-out;
  -webkit-transition: All 0.4s ease-in-out;
  -moz-transition: All 0.4s ease-in-out;
  -o-transition: All 0.4s ease-in-out;
}

/*  */
.center-icon {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 50px;
  height: 50px;
  margin-top: -50px;
  margin-left: -25px;
  /* transform: translateY(-50%); */
}

.center-icon img {
  position: relative;
  width: 100%;
  height: 100%;
}

/*  */
.bubble {
  position: absolute;
  bottom: 65px;
  left: 50%;
  width: 220px;
  padding: 15px;
  font-size: 13px;
  text-align: left;
  color: #fff;
  background-color: #fb995a;
  margin-left: -110px;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 1px 1px 10px rgba(251, 153, 90, 0.2);
}

.bubble .triangle {
  position: absolute;
  left: 50%;
  bottom: -6px;
  height: 20px;
  width: 20px;
  margin-left: -10px;
  background-color: #fb995a;
  transform: rotate(45deg);
}

.bubble .right-arrow {
  position: absolute;
  top: 50%;
  right: 15px;
  bottom: -6px;
  width: 18px;
  height: 18px;
  margin-left: -9px;
  margin-top: -9px;
  background-color: #fb995a;
}

.bubble .right-arrow img {
  width: 100%;
  height: 100%;
}

.bubble .text {
  position: relative;
  padding-right: 25px;
}

/*  */
.share-location {
  position: fixed;
  left: 50%;
  bottom: 42%;
  height: 30px;
  transform: translateX(-50%);
}

/*  */
.test {
  position: fixed;
  top: 90px;
  left: 20px;
  z-index: 9999;
}

.tabbar {
  z-index: 999;
}

.amap-marker-label {
  position: absolute;
  z-index: 2;
  border: 1px solid #ccc;
  background-color: white;
  white-space: nowrap;
  cursor: default;
  padding: 3px;
  font-size: 12px;
  line-height: 14px;
